@import url("https://fonts.googleapis.com/css?family=Roboto");
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: inherit; }

.app {
  width: 100vw;
  height: 100vh;
  background-color: #FDFCFD;
  color: #5F5B6B; }

.homepage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 16px; }
  .homepage__title {
    font-size: 100px;
    font-weight: bold; }
  .homepage__form {
    display: flex;
    flex-direction: column; }

.training {
  display: flex;
  justify-content: center;
  align-items: center; }

.button {
  padding: 8px 16px;
  border: 0;
  border-radius: 2px;
  background: #ffa726;
  color: white;
  font-size: 30px; }

.navigation {
  color: white;
  background: #182825; }
  .navigation__list {
    display: flex;
    justify-content: flex-end;
    margin: 0;
    list-style-type: none; }
    .navigation__list__item {
      margin: 20px;
      padding: 8px 16px;
      border: 0;
      border-radius: 2px;
      background: #7F7979; }

.question-box {
  margin: 16px;
  padding: 16px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2); }
  .question-box__question {
    font-size: 30px; }
  .question-box__answers {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    min-height: 300px;
    margin-top: 16px;
    color: white; }
    .question-box__answers__answer {
      padding: 16px 16px;
      width: 40%;
      background-color: #C1BDB3; }
      .question-box__answers__answer:hover {
        background-color: grey; }
    .question-box__answers__correct {
      background-color: greenyellow; }
    .question-box__answers__wrong {
      background-color: red; }

.timer {
  display: flex;
  align-items: center;
  margin: 16px;
  height: 50px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2); }
  .timer__bar {
    background-color: red;
    height: 10px; }



